/* BIM MODELING PROJECTS SLIDER */
/*   */
.slick-list {
    overflow: hidden;
}
/*   */
.slick-track{
    display: flex;
}

.section-projects {
    box-sizing: border-box;
    padding: 40px 0 90px;

    background-color: var(--pure-white-color);

    @media screen and (min-width: 768px) {
        padding: 60px 0 122px;
    }
    @media screen and (min-width: 1440px) {
        padding: 80px 0 122px;
    }
}

// .section-projects__block {

// }

.section-projects__title{
    @include font-title;

}

.section-projects__desc{
    @include font-body-reg;
    margin-top: 40px;

    @media screen and (min-width: 768px) {
        margin-top: 52px;
        width: 457px;
    }
    @media screen and (min-width: 1440px) {
        margin-top: 52px;
    }
}



.section-projects__slider {
    margin-top: 40px;

    @media screen and (min-width: 768px) {
        margin-top: 52px;
    }
    @media screen and (min-width: 1440px) {
        margin-top: 52px;
    }
}

.section-projects__slider-item {
    box-sizing: border-box;

    width: 320px;
    height: 160px;
    position: relative;

    @media screen and (min-width: 768px) {
        width: 704px;
        height: 328px;
    }
    @media screen and (min-width: 1440px) {
        width: 1136px;
        height: 560px;
    }
}

.section-projects__slider-item img{
    
    width: 320px;
    height: 160px;

    @media screen and (min-width: 768px) {
        width: 704px;
        height: 328px;
    }
    @media screen and (min-width: 1440px) {
        width: 1136px;
        height: 560px;
    }
}

