/*  () */
.section-projects__slider-item .slick-dots{
    display: flex;

    align-items: center;
    justify-content: center;
    gap: 36px;

    margin-top: 40px;

    @media screen and (min-width: 768px) {
        margin-top: 52px;
    }
    @media screen and (min-width: 1440px) {
        margin-top: 52px;
    }
}
.section-projects__slider-item .slick-dots li{
    list-style: none;
    
}

/*   */
.section-projects__slider-item .slick-dots li.slick-active {
    padding: 2px 0;
}

.section-projects__slider-item .slick-dots li.slick-active button{
    width: 6px;
    height: 6px;
}
/*   */
.section-projects__slider-item .slick-dots li button{
    display: block;
    font-size: 0;
    width: 10px;
    height: 10px;
    background-color: #0E0E0E;
    border: none;
    padding: 0;
    
}
