.final-thoughts{
    background-color: var(--pure-white-color);
}
.final-thoughts__title {
    text-align: center;
    margin-bottom: 20px;
    font-family: 'PT Mono';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: calc(20 / 20);
}
.final-thoughts__description{
    margin-bottom: 20px;
@include  font-body-reg;
}
.final-thoughts__description-last{
@include  font-body-reg;
}
.final-thoughts__item{
    @include font-body-reg;
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 20px;
    &::before {
      content: ' ';
      display: inline-flex;
      background-image: url(../images/vectors/vector-right.svg);
      width: 10px;
      height: 9px;
      background-size: cover;
      color: #cdcdcd;
      padding-right: 10px;
      
    }
}
.final-thoughts__list {
    margin-bottom: 20px;
}